import React, {Component} from 'react'
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import Badge from 'material-ui/Badge';
import IconButton from 'material-ui/IconButton';
import Done from 'material-ui/svg-icons/action/done';//icono de buenas
import TrendingDown from 'material-ui/svg-icons/action/trending-down';//icono de malas
import './AppGuariche.css'

function Recuadro(props){
  return(
    <span>{props.valor}</span>
  )
}

function Operacion(props){
  return(
    <span>+</span>
  )
}

function Resultado(props){
  return(
    <span>
      <input
        id='resultado'
        type='text'
        defaultValue = {props.resultado}
        onChange={props.onChange}
        onKeyPress={props.onKeyPress}/>
    </span>
  )
}

function Boton(props){
  return(
    <span>
      <input type='button' defaultValue='Comprobar' id='boton' onClick={props.onClick}/>
    </span>
  )
}

function BadgeBuena(props) {
  return(
    <span>
      <Badge badgeContent={props.buenas} primary={true}>
        <IconButton tooltip="Buenas">
          <Done />
        </IconButton>
      </Badge>
    </span>
  )
}

function BadgeMala(props) {
  return(
    <span>
      <Badge badgeContent={props.malas} secondary={true}>
        <IconButton tooltip="Malas">
          <TrendingDown />
        </IconButton>
      </Badge>
    </span>
  )
}

function Money(props) {
  return(
    <span>
      $ {props.money}
    </span>
  )
}

class AppGuariche extends Component {
  constructor(props) {
    super(props)
    this.state = {
      valor1: 1,
      valor2: 2,
      resultado: null,
      operacion: "+",
      buenas: 0,
      malas: 0,
      money: 0,
      rateMoneyUp: 5,
      rateMoneyDown: -15,
    }
    this.handleClick = this.handleClick.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.obtenerAleatorio = this.obtenerAleatorio.bind(this);
    this.handleKeyboardPress = this.handleKeyboardPress.bind(this);
  }

  handleClick(ev){
    if (ev.target.id === 'boton') {
      if (this.state.valor1+this.state.valor2 === this.state.resultado) {
        this.setState({
          buenas: this.state.buenas + 1,
          money: this.state.money + this.state.rateMoneyUp,
        })
      }else {
        this.setState({
          malas: this.state.malas + 1,
          money: this.state.money + this.state.rateMoneyDown,
        })
      }
    }
    this.setState({
      valor1: this.obtenerAleatorio(1,20),
      valor2: this.obtenerAleatorio(1,20),
    })
    document.getElementById('resultado').value = null
  }

  handleChange(ev){
    this.setState({
      resultado: parseInt(ev.target.value,10),
    })
  }

  handleKeyboardPress(ev){
    if (ev.charCode === 13) {
      if (this.state.valor1+this.state.valor2 === this.state.resultado) {
        this.setState({
          buenas: this.state.buenas + 1,
          money: this.state.money + this.state.rateMoneyUp,
          valor1: this.obtenerAleatorio(1,20),
          valor2: this.obtenerAleatorio(1,20),
        })
        document.getElementById('resultado').value = null
      }else {
        this.setState({
          malas: this.state.malas + 1,
          money: this.state.money + this.state.rateMoneyDown,
          valor1: this.obtenerAleatorio(1,20),
          valor2: this.obtenerAleatorio(1,20),
        })
        document.getElementById('resultado').value = null
      }
    }


  }

  obtenerAleatorio(min,max){
    return min + parseInt(Math.random()*(max-min),10)
  }

  componentWillMount() {
    this.setState({
      valor1: this.obtenerAleatorio(1,5),
      valor2: this.obtenerAleatorio(1,5),
    })
  }

  render(){
    return(
        <MuiThemeProvider>
          <div>
            <div>
              <Recuadro valor={this.state.valor1} />
              <Operacion operacion={this.state.operacion} />
              <Recuadro valor={this.state.valor2} />
              <span>=</span>
              <Resultado resultado={this.state.resultado} onChange={this.handleChange} onKeyPress={this.handleKeyboardPress}/>
            </div>
            <div>
              <Boton onClick={this.handleClick}/>
            </div>
            <div>
              <BadgeBuena buenas={this.state.buenas}/>
              <BadgeMala malas={this.state.malas}/>
              <Money money={this.state.money} />
            </div>
          </div>
        </MuiThemeProvider>
    )
  }
}

export default AppGuariche



